const _route = (name, path, scr, icon) => {
    return {
        path,
        name,
        icon,
        component: import(`@/views/${scr}.vue`),
    };
}

export default [
    _route('search', '/', 'Search', 'mdi-maginfy'),
    _route('about', '/about', 'About', 'mdi-information-outline'),
];